        } */
    grid-template-rows: 80px auto 50px;
  }
}

.subtitle {
  font-style: italic;
  color: #6c757d;
}

.subtitle em {
  font-family: Big Caslon,Book Antiqua,Palatino Linotype,Georgia,serif;
  color: #adb5bd;
}

[class*="columnsblock"] {
  display: grid;
  grid-gap: 1rem 1rem;
  align-items: center;
  justify-content: space-between;
  text-align: center;
}

.fa-heart {
  font-size: 2.5rem;
  color: #dc3545;
}

@media (min-width: 992px) {
  .columnsblock2 {
    grid-template-columns: repeat(2, 1fr);
